import React from 'react';

function Card ({header, content}) {

    return (
        <div className="card border-left-midnight shadow py-2">
            <div className="card-body pt-2 pb-0">
                <div className="d-block px-1">

                    <div className="d-flex justify-content-between mx-auto mb-2">
                        <div className="d-inline small font-weight-bold text-midnight">{header}</div>
                        <span className="badge badge-success px-4">Optimal</span>
                    </div>
 
                    <div className="d-flex justify-content-end align-text-top mx-auto">
                        <h1 className="d-inline display-4 mb-1">{content}</h1>
                        <h1 className="d-inline ml-1">°C</h1>
                    </div>

                </div>
            </div>
        </div>
    )

}

export default Card;