import React from 'react';

import Card from '../components/card';
import Nav from '../components/nav';
import {error} from './error';

function Dashboard() {

    return (
        /* <!-- Begin Page Content --> */
        <div className="container-fluid">

            {/* <!-- Page Heading --> */}
            <div className="d-flex align-items-center justify-content-between mb-3">
                <div className="d-none d-sm-inline h3 text-dark mb-0">Dashboard</div>

                <div className="d-none d-sm-inline">
                    <button className="btn btn-sm btn-warning btn-icon-split font-weight-bold" type="button" href="#">
                        <span className="icon text-white"><i className="fas fa-fw fa-sm fa-download"></i></span>
                        <span className="text px-4">Generate Report</span>
                    </button>
                </div>
                
            </div>

            {/* <!-- Content Row --> */}
            <div className="row mb-3">
                <div className="d-none d-sm-block col-md-12 col-lg-6 col-xl-4 mb-2">
                    <Card header="Evaporator Coil" content="12.76"/></div>
                <div className="d-none d-sm-block col-md-12 col-lg-6 col-xl-4 mb-2">
                    <Card header="Cabinet: Supply" content="23.22"/></div>
                <div className="d-none d-sm-block col-md-12 col-lg-6 col-xl-4 mb-2">
                    <Card header="Cabinet: Return" content="29.65"/></div>
            </div>

            {/* <!-- Datatable Tabs --> */}
            <div className="d-none d-md-block mb-3"><Nav /></div>

            {/* <!-- 404 Error Text --> */}
            <div className="d-block d-sm-none">
                {error ("Mobile view is not supported yet :(")}
            </div>
        </div>
    )
}

export default Dashboard;