import React from 'react';
import Authentication from '../assets/js/utils/Authentication';
import LocalStorageService from '../assets/js/utils/LocalStorageService';

function Topbar(props) {
    const logout = () => Authentication.logout(props);
    
    return (
        <div>
            {/* <!-- Topbar --> */}
            <nav className="navbar navbar-expand navbar-light bg-white topbar mb-3 static-top shadow">
                {/* <!-- Topbar Search --> */}
                <form className="navbar-search d-none d-md-block form-inline mx-2 mr-auto my-2">
                    <div className="input-group">
                        <input type="text" className="form-control bg-light border small" aria-label="Search" aria-describedby="basic-addon2" />
                        <div className="input-group-append">
                            <button className="btn btn-warning border" type="button">
                                <i className="fas fa-search fa-sm"></i>
                            </button>
                        </div>
                    </div>
                </form>

                {/* <!-- Topbar Navbar --> */}
                <ul className="navbar-nav ml-auto">

                    <li className="nav-item d-none d-md-block dropdown no-arrow mx-2">
                        <a className="nav-link dropdown-toggle" href="#" id="alertsdd" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <i className="fas fa-fw fa-bell text-midnight"></i>
                            <span className="badge badge-danger badge-pill badge-counter">36</span>
                        </a>
                    </li>

                    <div className="topbar-divider d-none d-md-block"></div>

                    {/* <!-- Nav Item - User Information --> */}
                    <li className="nav-item dropdown justify-content-center align-items-center">
                        <div className="nav-link" href="" id="userdd" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">

                            <a className="d-inline h1 img-profile text-midnight mr-4" href="">
                                <i className="fas fa-user-circle"></i></a>

                            <div className="nav-item d-inline">
                                <span className="d-block text-danger text-xs">Welcome!</span>
                                <a className="d-block dropdown-toggle text-dark font-weight-bold small" href="">
                                    {LocalStorageService.getUser()}</a>
                            </div>

                        </div>

                        {/* <!-- Dropdown - User Information --> */}
                        <div className="dropdown dropdown-menu dropdown-menu-right shadow animated--grow-in" aria-labelledby="userdd">
                            <div class="dropdown-header text-midnight">User Options</div>

                            <div className="dropdown-item navbar-text py-1 mb-1">
                                <h5 className="d-block text-center text-dark mb-1">{LocalStorageService.getUser()}</h5>
                                <span className="d-block badge badge-success px-4">General Administrator</span>
                            </div>
                            
                            <div className="dropdown-divider"></div>
                            <div className="dropdown-item"><i className="fas fa-fw fa-user mr-2 text-midnight"></i>
                                Profile</div>
                            <div className="dropdown-item"><i className="fas fa-fw fa-cogs mr-2 text-midnight"></i>
                                Account Settings</div>
                            <div className="dropdown-item"><i className="fas fa-fw fa-list mr-2 text-midnight"></i>
                                Activity Log</div>
                                
                            <div className="dropdown-divider"></div>
                            <div className="dropdown-item" data-toggle="modal" data-target="#logoutModal">
                                <i className="fas fa-fw fa-sign-out-alt mr-2 text-midnight"></i>Logout
                            </div>
                        </div>
                    </li>
                </ul>
            </nav>

            {/* <!-- Logout Modal--> */}
            <div className="modal fade" id="logoutModal" tabindex="-1" role="dialog" aria-labelledby="modalLabel" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header modal-title text-midnight font-weight-bold" id="modalLabel">End Session?</div>
                        <div className="modal-body">Select the <strong>Logout</strong> button below to end your session.</div>
                        <div className="modal-footer">
                            <button className="btn btn-sm btn-secondary font-weight-bold px-3" type="button" data-dismiss="modal">Cancel</button>
                            <button className="btn btn-sm btn-primary font-weight-bold px-3" type="button" data-dismiss="modal" onClick={logout}>
                                Logout</button>
                        </div>
                    </div>
                </div>
            </div>

            {/* <!-- End of Topbar --> */}
        </div>
    );
};

export default Topbar;