import axios from 'axios';
import LocalStorageService from './LocalStorageService';

const Authentication = (function() {
    const url = ('http://localhost:8000/auth/obtain-token');

    return ({
        login:(props, username, password) => {
            axios
                .post (url, {username: username, password: password})
                .then (response => {LocalStorageService.setAuthToken(response.data.token,
                    response.data.user); props.history.push('/');
                });
        },
        logout:(props) => {
            const authorization = {'Authorization': `Token ${LocalStorageService.getAuthToken()}`};
            axios
                .get (url, {headers: authorization})
                .then (response => {LocalStorageService.clear();
                    props.history.push('/auth');
                });
        }
    });
})();

export default Authentication;