import React from 'react';

function Error() {
    
    return (
        /* <!-- Begin Page Content --> */
        <div className="container-fluid">
            <div className="d-flex justify-content-center my-4">
                {error ("This page is under development!")}
            </div>
        </div>
    );
}

function error (info) {
    
    return (
        /* <!-- Begin Page Content --> */
        <div className="d-block text-center mb-5">

            {/* <!-- 404 Error Text --> */}
            <div className="lead text-secondary mb-0">Error</div>
            <div className="error text-dark mx-auto" data-text="404">404</div>
            <div className="lead text-secondary mb-0">Page Not Found</div>

            {/* <!-- Show message about page error --> */}
            <div className="small text-gray-500 mb-0">{info}</div>
        </div>
    )
}

export default Error;
export {error};