/* *Local Storage Service */
import CryptoJS from 'crypto-js';

const Cipher = (function () {
    const SECRET_KEY = ('6d71e3d01298bc4cc52b726ce62c927da91c3f25');
    return ({
        encrypt:(item) => {
            return CryptoJS.AES.encrypt(item, SECRET_KEY).toString();
        },
        decrypt_utf8:(item) => {
            return CryptoJS.AES.decrypt(item, SECRET_KEY).toString(
                CryptoJS.enc.Utf8);
        },
        cipher:(item) => {
            return CryptoJS.AES.decrypt(CryptoJS.AES.encrypt(item,
                SECRET_KEY), SECRET_KEY).toString();
        }
    })
})();

const LocalStorageService = (function() {
    const ciphered_token = Cipher.cipher('token');
    const ciphered_user = Cipher.cipher('user');
    return ({
        getAuthToken:() => {
            const encryptedToken = localStorage.getItem(ciphered_token)
            return (encryptedToken!=null ? Cipher.decrypt_utf8(encryptedToken) : null)
        },
        getUser:() => {
            const encryptedUser = localStorage.getItem(ciphered_user)
            return (encryptedUser!=null ? Cipher.decrypt_utf8(encryptedUser) : null)
        },
        clear:() => localStorage.clear(),
        setAuthToken: (accessToken, user) => {
            localStorage.setItem(ciphered_token, Cipher.encrypt(accessToken));
            localStorage.setItem(ciphered_user, Cipher.encrypt(user));
        }
    });
})();

export default LocalStorageService;